.content__mainimg {
    width:100%;
    height: 300px;
    border-radius: 3px;
}
.fullName{
    font-size:16px;
    font-weight:bold;
}
.useravatar {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 30px;
}
.line{
    width: 80%;
    height: 2px;
    background-color: #370049;
    margin: 0 auto;

}
.profile {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 50px;
    padding-top: 30px;


}

.userdescription {
    text-align: left;
    word-wrap: break-spaces;
    font-size: 14px;
    padding: 25px;
}
.butt{
    width:300px;

}
@media (max-width:850px){
    .content__mainimg{
        display: none;
    }
    .profile{
        flex-direction:column;
        align-items: center;
        margin:0;
    }
.useravatar {
    padding-top: 40px;
    width: 40%;
    height: 40%;
}
.line{
    display:none;
}
}
